<template>
  <v-app dark v-scroll="handleScroll">
    <v-content>
      <Header></Header>
      <Navbar></Navbar>
      <Profile></Profile>
      <Skills></Skills>
      <Projects></Projects>
      <Footer></Footer>
      <v-fab-transition>
        <v-btn
          v-show="!hidden"
          @click="$vuetify.goTo('#home', options)"
          fixed
          dark
          fab
          bottom
          right
          color="primary"
        >
          <v-icon>keyboard_arrow_up</v-icon>
        </v-btn>
      </v-fab-transition>
    </v-content>
  </v-app>
</template>

<script>
import Header from "./components/Header";
import Navbar from "./components/Navbar";
import Profile from "./components/Profile";
import Skills from "./components/Skills";
import Projects from "./components/Projects";
import Footer from "./components/Footer";
export default {
  name: "App",
  components: {
    Header,
    Navbar,
    Profile,
    Skills,
    Projects,
    Footer
  },
  data() {
    return {
      hidden: true,
      skillsOffsetTop: null
    };
  },
  methods: {
    handleScroll: function() {
      if (window.scrollY >= this.skillsOffsetTop) {
        this.hidden = false;
      } else {
        this.hidden = true;
      }
    }
  },
  mounted() {
    this.skillsOffsetTop = document.getElementById("skills").offsetTop;
    this.$vuetify.theme.primary = "#006ca3";
  }
};
</script>
