<template>
  <div class="bg">
    <v-container id="skills">
      <v-layout justify-center>
        <div class="section-title mb-2">
          <h1 class="d-block display-2 text-xs-center mb-4 grey--text text--lighten-4">Skills</h1>
          <span class="d-block underline"></span>
        </div>
      </v-layout>
      <v-layout align-center justify-center fill-height wrap>
        <v-flex xs6 md3>
          <div class="box">
            <div id="html" class="progress-bar"></div>
            <p class="grey--text text--lighten-2">HTML</p>
          </div>
        </v-flex>
        <v-flex xs6 md3>
          <div class="box">
            <div id="css" class="progress-bar"></div>
            <p class="grey--text text--lighten-2">CSS</p>
          </div>
        </v-flex>
        <v-flex xs6 md3>
          <div class="box">
            <div id="javascript" class="progress-bar"></div>
            <p class="grey--text text--lighten-2">Javascript</p>
          </div>
        </v-flex>
        <v-flex xs6 md3>
          <div class="box">
            <div id="php" class="progress-bar"></div>
            <p class="grey--text text--lighten-2">PHP</p>
          </div>
        </v-flex>

        <v-flex xs6 md3>
          <div class="box">
            <div id="laravel" class="progress-bar"></div>
            <p class="grey--text text--lighten-2">Laravel</p>
          </div>
        </v-flex>
        <v-flex xs6 md3>
          <div class="box">
            <div id="symfony" class="progress-bar"></div>
            <p class="grey--text text--lighten-2">Symfony</p>
          </div>
        </v-flex>
        <v-flex xs6 md3>
          <div class="box">
            <div id="nodejs" class="progress-bar"></div>
            <p class="grey--text text--lighten-2">Node.js</p>
          </div>
        </v-flex>
        <v-flex xs6 md3>
          <div class="box">
            <div id="vuejs" class="progress-bar"></div>
            <p class="grey--text text--lighten-2">Vue.js</p>
          </div>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import ProgressBar from 'progressbar.js';

// const htmlBox = document.getElementById('html');

export default {
  data() {
    return {
      skillsOffsetTop: null,
      progressbarOptions: {}
    };
  },
  methods: {},
  mounted() {
    const htmlBox = document.getElementById('html');
    const cssBox = document.getElementById('css');
    const javascriptBox = document.getElementById('javascript');
    const phpBox = document.getElementById('php');
    const laravelBox = document.getElementById('laravel');
    const symfonyBox = document.getElementById('symfony');
    const nodejsBox = document.getElementById('nodejs');
    const vuejsBox = document.getElementById('vuejs');

    this.skillsOffsetTop = document.getElementById('skills').offsetTop;

    this.progressbarOptions = {
      color: '#aaa',
      // This has to be the same size as the maximum width to
      // prevent clipping
      strokeWidth: 4,
      trailWidth: 1,
      easing: 'easeInOut',
      duration: 1400,
      text: {
        autoStyleContainer: false
      },
      from: { color: '#006ca3', width: 1 },
      to: { color: '#006ca3', width: 4 },
      // Set default step function for all animate calls
      step: function(state, circle) {
        circle.path.setAttribute('stroke', state.color);
        circle.path.setAttribute('stroke-width', state.width);

        var value = Math.round(circle.value() * 100);
        if (value === 0) {
          circle.setText('');
        } else {
          circle.setText(`${value}%`);
        }
      }
    };

    const font = '"Raleway", Helvetica, sans-serif';
    const size = '2rem';

    // HTML Progress Bar
    const htmlCircle = new ProgressBar.Circle(htmlBox, this.progressbarOptions);
    htmlCircle.text.style.fontFamily = font;
    htmlCircle.text.style.fontSize = size;

    htmlCircle.animate(0.9); // Number from 0.0 to 1.0

    // CSS Progress Bar
    const cssCircle = new ProgressBar.Circle(cssBox, this.progressbarOptions);
    cssCircle.text.style.fontFamily = font;
    cssCircle.text.style.fontSize = size;

    cssCircle.animate(0.8); // Number from 0.0 to 1.0

    // JavaScript Progress Bar
    const javascriptCircle = new ProgressBar.Circle(
      javascriptBox,
      this.progressbarOptions
    );
    javascriptCircle.text.style.fontFamily = font;
    javascriptCircle.text.style.fontSize = size;

    javascriptCircle.animate(0.7); // Number from 0.0 to 1.0

    // PHP Progress Bar
    const phpCircle = new ProgressBar.Circle(phpBox, this.progressbarOptions);
    phpCircle.text.style.fontFamily = font;
    phpCircle.text.style.fontSize = size;

    phpCircle.animate(0.7); // Number from 0.0 to 1.0

    // Laravel Progress Bar
    const laravelCircle = new ProgressBar.Circle(
      laravelBox,
      this.progressbarOptions
    );
    laravelCircle.text.style.fontFamily = font;
    laravelCircle.text.style.fontSize = size;

    laravelCircle.animate(0.5); // Number from 0.0 to 1.0

    // Symfony Progress Bar
    const symfonyCircle = new ProgressBar.Circle(
      symfonyBox,
      this.progressbarOptions
    );
    symfonyCircle.text.style.fontFamily = font;
    symfonyCircle.text.style.fontSize = size;

    symfonyCircle.animate(0.35); // Number from 0.0 to 1.0

    // Node.js Progress Bar
    const nodejsCircle = new ProgressBar.Circle(
      nodejsBox,
      this.progressbarOptions
    );
    nodejsCircle.text.style.fontFamily = font;
    nodejsCircle.text.style.fontSize = size;

    nodejsCircle.animate(0.4); // Number from 0.0 to 1.0

    // Vue.js Progress Bar
    const vuejsCircle = new ProgressBar.Circle(
      vuejsBox,
      this.progressbarOptions
    );
    vuejsCircle.text.style.fontFamily = font;
    vuejsCircle.text.style.fontSize = size;

    vuejsCircle.animate(0.3); // Number from 0.0 to 1.0
  }
};
</script>


<style scoped>
.progress-bar {
  margin: 20px;
  width: 100px;
  height: 100px;
  position: relative;
}

.box {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.bg {
  background-color: #383838;
}

#skills {
  margin-top: 6rem;
}
</style>
