<template>
  <v-toolbar id="navbar" class="main-nav">
    <v-layout class="nav-layout" justify-center fill-height>
      <v-toolbar-items class="menu">
        <v-btn @click="$vuetify.goTo('#profile', options)" flat>Profile</v-btn>
        <v-btn @click="$vuetify.goTo('#skills', options)" flat>Skills</v-btn>
        <v-btn @click="$vuetify.goTo('#projects', options)" flat>Projects</v-btn>
      </v-toolbar-items>
    </v-layout>
  </v-toolbar>
</template>

<script>
import * as easings from "vuetify/es5/util/easing-patterns";

export default {
  data() {
    return {
      duration: 1000,
      offset: 150,
      easing: "easeInOutCubic",
      easings: Object.keys(easings)
    };
  },
  computed: {
    options() {
      return {
        duration: this.duration,
        offset: this.offset,
        easing: this.easing
      };
    }
  },
  mounted() {
    window.onscroll = function() {
      myFunction();
    };

    var navbar = document.getElementById("navbar");
    var sticky = navbar.offsetTop;

    function myFunction() {
      if (window.pageYOffset >= sticky) {
        navbar.classList.add("sticky");
      } else {
        navbar.classList.remove("sticky");
      }
    }
  }
};
</script>


<style scoped>
.sticky {
  position: fixed;
  top: 0;
  z-index: 9797;
}
.nav-layout {
  margin: auto;
  max-width: 1200px;
}

.menu > button:hover {
  color: var(--blue) !important;
  align-self: center !important;
}
</style>
